interface TestResult {
  status: "success" | "error";
  message: string;
  statusCode?: number;
  details?: Record<string, unknown>;
}

interface ApiTestResultDisplayProps {
  result: TestResult | null;
}

export default function ApiTestResultDisplay({
  result,
}: ApiTestResultDisplayProps) {
  if (!result) return null;

  return (
    <div
      className={`mt-6 p-4 rounded-md ${
        result.status === "success"
          ? "bg-green-50 border border-green-200"
          : "bg-red-50 border border-red-200"
      }`}
    >
      <div className="flex">
        <div className="flex-shrink-0">
          {result.status === "success" ? (
            <svg
              className="h-5 w-5 text-green-400"
              viewBox="0 0 20 20"
              fill="currentColor"
            >
              <path
                fillRule="evenodd"
                d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z"
                clipRule="evenodd"
              />
            </svg>
          ) : (
            <svg
              className="h-5 w-5 text-red-400"
              viewBox="0 0 20 20"
              fill="currentColor"
            >
              <path
                fillRule="evenodd"
                d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z"
                clipRule="evenodd"
              />
            </svg>
          )}
        </div>
        <div className="ml-3">
          <h3
            className={`text-sm font-medium ${
              result.status === "success" ? "text-green-800" : "text-red-800"
            }`}
          >
            {result.status === "success" ? "Success" : "Error"}
          </h3>
          <div className="mt-2 text-sm text-gray-700">
            <p>{result.message}</p>
            {result.statusCode && (
              <p className="mt-1">Status Code: {result.statusCode}</p>
            )}
            {result.details && (
              <pre className="mt-2 text-xs bg-gray-100 p-2 rounded overflow-x-auto max-h-[300px] overflow-y-auto w-full">
                {JSON.stringify(result.details, null, 2)}
              </pre>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
